@import "../../_mixins";
.footer {
  max-width: 1270px;
  min-height: 340px;
  margin: 81px auto 69px;
  text-align: center;
  position: relative;
  z-index: 20;

  .footer_vector17 {
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0%;
    top: 78%;
  }
  .footer_vector18 {
    position: absolute;
    width: 1440px;
    height: 100%;
    left: -171%;
    top: -8%;
  }
  .footer_vector19 {
    position: absolute;
    width: 1440px;
    height: 59%;
    left: -131%;
    top: -40%;
  }

  .footer_vector20,
  .footer_vector21,
  .footer_vector22 {
    display: none;
  }

  .footer-section {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    position: relative;
    z-index: 15;

    .footer-section__logo {
      
      filter: drop-shadow(0px 10px 6px rgba(0, 0, 0, 0.25));
      
    }

    .footer-section__services,
    .footer-section__info {
      padding: 10px;
      display: none;
    }
    .footer-section__social {
      display: flex;
      flex-direction: column;
      align-items: center;

      .social_title {
        font-weight: 700;
        font-size: 16px;
        line-height: 20px;
        color: #ffffff;
        margin: 56px 0 29px;
      }
      .social__items {
        display: flex;
        gap: 20px;

        .social__items_item {
          width: 32px;
          height: 32px;
          cursor: pointer;
          z-index: 15;
          transition: 0.3s;
          &:hover {
            transform: scale(1.5);
          }
        }
      }
      .social_button {
        @include button(295px, 14px, 17px);
        margin: 47px 0;

        &:hover {
          border: 2px solid #ffffff;
          padding: 16px 0px;
          color: #ffffff;
          background: #302c42;
        }
      }
    }
  }

  .footer__rights {
    font-weight: 700;
    font-size: 14px;
    line-height: 250%;
    letter-spacing: 0.13em;
    color: #ffffff;
    margin-top: 38px;
  }
}

@include desktop() {
  .footer {
    margin: 150px auto 60px;

    .footer_vector17 {
      position: absolute;
      width: 100%;
      height: 100%;
      left: 0%;
      top: 78%;
    }
    .footer_vector18 {
      position: absolute;
      width: 100%;
      height: 100%;
      left: -6%;
      top: -8%;
    }
    .footer_vector19 {
      position: absolute;
      width: 100%;
      height: 100%;
      left: -8%;
      top: -79%;
    }

    .footer_vector20 {
      display: block;
      position: absolute;
      width: 100%;
      height: 100%;
      left: 21%;
      top: 2%;
    }
    .footer_vector21 {
      display: block;
      position: absolute;
      width: 100%;
      height: 100%;
      left: 46%;
      top: 2%;
    }
    .footer_vector22 {
      display: block;
      position: absolute;
      width: 100%;
      height: 100%;
      left: 69%;
      top: 2%;
    }

    .footer-section {
      flex-direction: row;
      justify-content: space-between;

      .footer-section__logo {
        filter: drop-shadow(0px 10px 6px rgba(0, 0, 0, 0.25));
      }

      .footer-section__services,
      .footer-section__info {
        padding: 10px;
        display: block;

        .footer-menu {
          display: flex;
          flex-direction: column;
          list-style: none;
          text-align: left;
          gap: 23px;

          .footer-menu__link {
            text-transform: uppercase;
            text-decoration: none;
            font-weight: 700;
            font-size: 16px;
            line-height: 20px;
            color: #ffffff;
            &:hover {
              text-decoration: underline;
            }
          }
          .footer-menu__link_active {
            text-transform: uppercase;
            font-weight: 700;
            font-size: 16px;
            line-height: 20px;
            color: #c0b7e8;
            text-decoration: none;
          }
        }
      }

      .footer-section__social {
        display: flex;
        flex-direction: column;
        align-items: start;

        .social_title {
          margin: 10px 0 39px;
        }

        .social_button {
          @include button(214px, 12px, 15px);
          margin: 49px 0;
        }
      }
    }

    .footer__rights {
      line-height: 17px;
      margin-top: 126px;
    }
  }
}
