@import "../../_mixins";


@include desktop() {
  .slider{
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 58px auto;
  padding: 50px 45px;
  max-width: 1260px;
  background: radial-gradient(50% 2900.76% at 50% 53.89%, rgba(58, 52, 86, 0.95) 0%, #211E2E 100%);
  box-shadow: 0px 4px 4px rgba(192, 183, 232, 0.01);
  border-radius: 90px;
  position: relative;
  z-index: 25;

  &__separator1{
    position: absolute;
    width: 5px;
    margin-top: 18px;
    top: 50%;
    left: 39%;
    transform: translate(-50%, -50%);
  }
    
   &__separator2{
    position: absolute;
    width: 5px;
    margin-top: 18px;
    top: 50%;
    right: 35%;
    transform: translate(-50%, -50%);
  }
}

}